import classNames from 'classnames';

import Target from 'shared/components/target/0.2';
import Dropzone from 'shared/components/dropzone/0.4';
import Draggable from 'shared/components/draggable/0.4';

const objects = [
    'umbrella',
    'glasses',
    'tire',
    'shovel',
    'link',
    'bucket',
    'boots',
    'gloves',
    'whistle',
    'cup',
    'phone',
    'piece',
    'tooth',
    'ball',
];

const targets = [
    'tire',
    'link',
    'cup',
    'phone',
    'tooth',
];

export default function (props, ref, key) {
    var onStart;
    var startGame;
    var closeReveal;
    var onCorrect;
    var onPrinted;
    var reset;
    var onTransitionEnd;

    onStart = function () {
        _.each(this.refs.bottom.refs.slider.refs, slide => {
            _.each(slide.refs, draggable => {
                draggable.markIncorrect();
            });
        });
    };

    startGame = function () {
        skoash.trigger('updateState', {
            path: 'reveal',
            data: {
                open: 'drag-it-here',
            },
        });
    };

    closeReveal = function () {
        skoash.trigger('updateState', {
            path: 'reveal',
            data: {
                close: true,
            },
        });

        if (_.get(props, 'data.setTarget', 0) > 4) {
            skoash.trigger('goto', {
                index: parseInt(props.index, 10) + 1,
            });
        }
    };

    onCorrect = function (dropped) {
        skoash.trigger('updateState', {
            path: 'printed',
            data: dropped,
        });
        skoash.trigger('updateState', {
            path: 'sfx',
            data: {
                playing: 'print'
            },
        });
    };

    onPrinted = function () {
        var dropped = _.get(props, 'data.printed');

        if (dropped.props.message === _.get(props, 'data.target.object.props.name')) {
            dropped.markCorrect();
            skoash.trigger('updateState', {
                path: 'transition',
                data: true,
            });
        } else {
            dropped.markIncorrect();
            reset();
            skoash.trigger('updateState', {
                path: 'sfx',
                data: {
                    playing: 'incorrect',
                },
            });
            skoash.trigger('updateState', {
                path: 'reveal',
                data: {
                    open: 'try-again',
                },
            });
        }
    };

    reset = function () {
        _.each(['printed', 'transition', 'layer1', 'layer2', 'layer3'], (v) => {
            skoash.trigger('updateState', {
                path: v,
                data: false,
            });
        });
    };

    onTransitionEnd = function () {
        if (!_.get(props, 'data.transition')) return;
        skoash.trigger('updateState', {
            path: 'sfx',
            data: {
                playing: 'correct',
            },
        });
        skoash.trigger('updateState', {
            path: 'reveal',
            data: {
                open: _.get(props, 'data.printed.props.message'),
            },
        });
        if (_.get(props, 'data.setTarget', 0) < 4) reset();
        skoash.trigger('updateState', {
            path: 'setTarget',
            data: _.get(props, 'data.setTarget', 0) + 1,
        });
    };

    return (
        <skoash.Screen
            {...props}
            ref={ref}
            key={key}
            id="printer"
            onStart={onStart}
        >
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'SpritesAnimations/sprite.game2.carouselarrow.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'SpritesAnimations/sprite.game2.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/img.printer.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/img.leftbox.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'SpritesAnimations/sprite.game2.brokenobj.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/img.winframe.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/img.popup.png'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/bkg.3.jpg'}
            />
            <skoash.Image
                className="hidden"
                src={ENVIRONMENT.MEDIA + 'ImageAssets/img.sparkle.png'}
            />
            <skoash.MediaCollection
                play={_.get(props, 'data.sfx.playing')}
            >
                <skoash.MediaSequence
                  ref="print"
                  silentOnStart
                >
                    <skoash.Audio
                        type="sfx"
                        playTarget="layer1"
                        src={ENVIRONMENT.MEDIA + 'SoundAssets/effects/Printing.mp3'}
                        sprite={[0, 1700]}
                    />
                    <skoash.Audio
                        type="sfx"
                        playTarget="layer2"
                        src={ENVIRONMENT.MEDIA + 'SoundAssets/effects/Printing.mp3'}
                        sprite={[0, 1700]}
                    />
                    <skoash.Audio
                        type="sfx"
                        playTarget="layer3"
                        src={ENVIRONMENT.MEDIA + 'SoundAssets/effects/Printing.mp3'}
                        onComplete={onPrinted}
                        sprite={[0, 1700]}
                    />
                </skoash.MediaSequence>
                <skoash.Audio
                    type="sfx"
                    ref="correct"
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/effects/Correct2.mp3'}
                />
                <skoash.Audio
                    type="sfx"
                    ref="incorrect"
                    complete
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/effects/Incorrect2.mp3'}
                />
            </skoash.MediaCollection>
            <skoash.MediaCollection
                play={_.get(props, 'data.reveal.open')}
            >
                <skoash.Audio
                    type="voiceOver"
                    ref="instructions"
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_drag_item.mp3'}
                />
                <skoash.Audio
                    type="voiceOver"
                    ref={targets[0]}
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_level1_great.mp3'}
                />
                <skoash.Audio
                    type="voiceOver"
                    ref={targets[1]}
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_level2_amazing.mp3'}
                />
                <skoash.Audio
                    type="voiceOver"
                    ref={targets[2]}
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_level3_excellent.mp3'}
                />
                <skoash.Audio
                    type="voiceOver"
                    ref={targets[3]}
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_level4_one_more.mp3'}
                />
                <skoash.Audio
                    type="voiceOver"
                    ref={targets[4]}
                    src={ENVIRONMENT.MEDIA + 'SoundAssets/vos/VO_level5_wizard.mp3'}
                />
            </skoash.MediaCollection>
            <skoash.Component className="targets">
                <div>
                    What <span>ITEM</span> can<br/>
                    solve this problem?
                </div>
                <Target
                    setTarget={_.get(props, 'data.setTarget', 0)}
                    checkComplete={false}
                    complete={_.get(props, 'data.setTarget', 0) > 4}
                    targets={[
                        <skoash.Component name={targets[0]} />,
                        <skoash.Component name={targets[1]} />,
                        <skoash.Component name={targets[2]} />,
                        <skoash.Component name={targets[3]} />,
                        <skoash.Component name={targets[4]} />,
                    ]}
                />
            </skoash.Component>
            <Dropzone
                dropped={_.get(props, 'data.draggable.dropped')}
                dropzones={[
                    <skoash.Component
                    answers={objects}
                    className={classNames(_.get(props, 'data.printed.props.message'), {
                        transition: _.get(props, 'data.transition'),
                        layer1: _.get(props, 'data.layer1.playing'),
                        layer2: _.get(props, 'data.layer2.playing'),
                        layer3: _.get(props, 'data.layer3.playing'),
                    })}
                    onTransitionEnd={onTransitionEnd}
                  />
                ]}
                onCorrect={onCorrect}
            />
            <skoash.Component ref="bottom" className="bottom">
                <div>
                    <span>DRAG AND DROP</span> the item onto the printer above
                </div>
                <skoash.Slider ref="slider">
                    <skoash.Component>
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[0]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[1]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[2]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[3]}
                        />
                    </skoash.Component>
                    <skoash.Component>
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[4]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[5]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[6]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[7]}
                        />
                    </skoash.Component>
                    <skoash.Component>
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[8]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[9]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[10]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[11]}
                        />
                    </skoash.Component>
                    <skoash.Component>
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[12]}
                        />
                        <Draggable
                            returnOnIncorrect
                            stayOnCorrect={false}
                            message={objects[13]}
                        />
                    </skoash.Component>
                </skoash.Slider>
            </skoash.Component>
            <skoash.Reveal
                openTarget="reveal"
                openOnStart="instructions"
                openReveal={_.get(props, 'data.reveal.open')}
                closeReveal={_.get(props, 'data.reveal.close')}
                list={[
                    <skoash.Component
                        type="li"
                        data-ref="instructions"
                    >
                        <h3>
                              Instructions
                        </h3>
                        <div>
                            <span>DRAG</span> the item onto the<br/>
                            3D printer that is the solution<br/>
                            for each situation.
                        </div>
                        <button onClick={startGame} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        data-ref="drag-it-here"
                        onClick={closeReveal}
                    >
                        <div>
                            Drag it here.<br/>
                            Press anywhere on the screen<br/>
                            to continue.
                        </div>
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        ref={targets[0]}
                    >
                        <h3>
                            GREAT JOB!
                        </h3>
                        <div>
                            Lets see if you can<br/>
                            figure out this next one!
                        </div>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        ref={targets[1]}
                    >
                        <h3>
                            YOU HAVE AMAZING<br/>
                            PROBLEM-SOLVING<br/>
                            SKILLS!
                        </h3>
                        <div>
                            But this one might be harder
                        </div>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        ref={targets[2]}
                    >
                        <h3>
                            EXCELLENT<br/>
                            WORK!
                        </h3>
                        <div>
                            Can you solve this next one?
                        </div>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        ref={targets[3]}
                    >
                        <h3>
                            YOUVE DONE<br/>
                            IT AGAIN!
                        </h3>
                        <div>
                            Just one more to go!
                        </div>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        ref={targets[4]}
                    >
                        <h3>
                            YOURE A<br/>
                            3D PRINTING<br/>
                            WIZARD
                        </h3>
                        <div>
                            and have solved all the problems!<br/>
                            Thanks for playing!
                        </div>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                    <skoash.Component
                        type="li"
                        data-ref="try-again"
                    >
                        <h3>
                            TRY<br/>
                            AGAIN
                        </h3>
                        <button onClick={closeReveal} />
                    </skoash.Component>,
                ]}
            />
        </skoash.Screen>
    );
}



